#main_banner{background: url(../../public/images/main/main_banner3.png) no-repeat;width:100%; padding-bottom: 40%; background-size:cover;}
#main_banner p{color:black;font-size: 30px;position: absolute; top:110px; left:100px;text-shadow: 1px 1px white;}

#oa1{width:250px; height:250px; margin-top: 10px; margin-left: 70px;}
#oa1 p:nth-child(1){margin-left: 25px;}
#oa1 p:nth-child(2){text-align: center; font-size: 20px;margin-top: 10px; font-weight: bold;}
#oa1 p:nth-child(3){text-align: center; font-size: 15px;margin-top: 10px; font-weight: bold; color:gray;}

#oa2{width:250px; height:250px; margin-top: 100px; margin-left: 70px;}
#oa2 p:nth-child(1){margin-left: 25px;}
#oa2 p:nth-child(2){text-align: center; font-size: 20px;margin-top: 10px; font-weight: bold;}
#oa2 p:nth-child(3){text-align: center; font-size: 15px;margin-top: 10px; font-weight: bold; color:gray;}

#oa3{width:250px; height:250px; margin-top: 100px; margin-left: 70px;}
#oa3 p:nth-child(1){margin-left: 25px;}
#oa3 p:nth-child(2){text-align: center; font-size: 17px;margin-top: 10px; font-weight: bold;}
#oa3 p:nth-child(3){text-align: center; font-size: 15px;margin-top: 10px; font-weight: bold; color:gray;}

#oa4{width:100%; height:auto; border-top: 2px solid black; margin-top: 70px;}
#oa4 p:nth-child(1){font-size: 17px; margin:50px 0; width:auto; font-weight: bold; text-align: center;}
#oa4 p:nth-child(2) img{margin-left: 50%; margin-left: 70px;}
#oa4 p:nth-child(3) img{margin-left: 50%; margin-left: 70px;}

.but{width:240px; height:50px; background-color: darkgrey; margin-left: 80px; margin-top: 20px;margin-bottom: 20px; border-radius: 5px;}
.but li{line-height: 50px; text-align: center; font-size: 20px; font-weight: bold;}
.but li a{color:black;}
.but li a:hover{color:white;}