#main_banner{background: url(../../public/images/main/main_banner1.png) no-repeat; width:100%; padding-bottom: 40%; background-size:cover; position: relative;}
#main_banner p{color:white;}
#main_banner p:first-child{font-size: 30px;position: absolute;top:10px; left:20px;}
#main_banner p:last-child{font-size: 15px;position: absolute;top:65px; left:20px;}

#icon{width:100%; height: 100px; margin-top: 15px; margin-bottom: 20px;}
#icon ul li{float: left; width:90px; height:90px; margin-left: 4px;font-size: 13px; text-align: center; font-weight: bold;}
#icon ul li img{width:40px; height:40px;}

h2{margin-bottom: 10px; margin-left: 5px;font-size: 20px;}
.service1, .service2, .service3{margin:20px 0 10px 10px; position: relative;}
.service1 p:last-child,.service2 p:last-child,.service3 p:last-child{font-size: 25px; position: absolute; top:80px; left: 35px; color: white;}