#main_banner{background: url(../../public/images/ConnectPro/main_banner.png) no-repeat; width:100%; padding-bottom: 40%; background-size:cover; position: relative;}
#main_banner p{color:black;text-shadow: 1px 1px white;}
#main_banner p:first-child{font-size: 40px;position: absolute;top:40px; left:100px;}
#main_banner p:last-child{font-size: 18px;position: absolute;top:100px; left:80px;}

#coment1{width:390px; height:auto; margin-top: 20px;}
