#main_banner{background: url(../../public/images/main/main_banner4.png) no-repeat;width:100%; padding-bottom: 40%; background-size:cover;}
#main_banner p{color:black;font-size: 30px; position: absolute;top:115px; left:140px;text-shadow: 1px 1px white;}

h2{margin-left: 50%; transform: translate(-50%,0); text-align: center; margin-top: 30px; margin-bottom: 30px; font-size: 22px;}
.a_box1{ background-color: #faf8f8; width:100%; height:200px;}
.a_box1 p{text-align: center; padding-top: 75px; font-size: 10px; line-height: 20px; font-weight: bold;}

.a_box2{ background-color: #faf8f8; width:100%; height:200px;}
.a_box2 p{text-align: center; font-size: 18px; line-height: 200px; font-weight: bold;}

.a_box3{ background-color: #faf8f8; width:100%; height:200px;}
.a_box3 p{text-align: center; padding-top: 75px; font-size: 18px; line-height: 20px; font-weight: bold;}

.a_box4{ background-color: #faf8f8; width:100%; height:200px; margin-bottom: 50px;}
.a_box4 p{text-align: center; font-size: 18px; line-height: 200px; font-weight: bold;}

.a_box5{ background-color: #faf8f8; width:100%; height:230px; margin-bottom: 50px;}
.a_box5 p{margin-left: 15px;}
.a_box5 p:nth-child(1){color:red; font-size: 20px; padding-top: 20px; font-weight: bold;}
.a_box5 p:nth-child(2){font-size: 20px; margin-top: 15px;}
.a_box5 p:nth-child(2) span{color:#999999;}
.a_box5 p:nth-child(3){color:#999999; font-size: 18px; margin-top: 15px; line-height: 25px;}
.a_box5 p:nth-child(4) img{float: right; margin-right: 20px;}

.a_box6{ background-color: #faf8f8; width:100%; height:190px;}
.a_box6 p{margin-left: 15px;}
.a_box6 p:nth-child(1){color:red; font-size: 20px; padding-top: 20px; font-weight: bold;}
.a_box6 p:nth-child(2){font-size: 20px; margin-top: 15px;}
.a_box6 p:nth-child(2) span{color:#999999;}
.a_box6 p:nth-child(3) img{float: right; margin-right: 20px;}

.text{color: red; font-size: 10px; margin-top: 5px; margin-bottom: 20px; margin-left: 10px;}