#main_banner{background: url(../../public/images/membership/main_banner.png) no-repeat; width:100%; padding-bottom: 40%; background-size:cover; position: relative;}
#main_banner p{color:black;text-shadow: 1px 1px white;}
#main_banner p:first-child{font-size: 35px;position: absolute;top:50px; left:110px;}

h2{font-size: 25px; width: 100%; text-align:center;}

#membership1{background-color: #fee9c0; width:100%; height: auto; border-top: 2px solid black;border-bottom: 2px solid black; margin-top: 30px;}
#membership1 p{line-height: 20px;}
#membership1 p:nth-child(1){ text-align:center; padding-top:10px ; padding-bottom:10px;}
#membership1 p:nth-child(2){ text-align:center; margin-bottom: 10px; font-size: 20px; font-weight: bold;}
#membership1 p:nth-child(3){border-top: 2px solid black;font-size: 13px; padding-left: 90px; padding-top: 10px; padding-bottom: 10px;} 

