.but{border:2px solid red; width: 280px; border-radius: 10px; background-color: #fecfc0; height:40px; margin-left: 50%; transform: translate(-50%,0); margin-top: 30px; margin-bottom: 30px;}
.but li{font-size: 20px; text-align: center; line-height: 40px; font-weight: bold;}
.but li a{color:black;}
.but li a:hover{ color:white;}