.all_menu{float:right; width:10%; height:60px;}
.all_menu img{margin-top:13px;}

#top{width:100%; min-width:320px; height:60px; background-color: #d9d9d9;}
#top h1 img{position:absolute; left:50%;top:10px;transform:translate(-50%,0);}

.menu_open{width:50%; height:100%; background-color: rgba(0,0,0,0.9); position:fixed; right:-100%; top:0; z-index: 10; overflow:hidden; transition:right 0.5s;}
.menu{margin-top:100px;}
.menu li{text-indent: 2em; line-height: 2.1em;}
.menu li a{color:#ffffff;}
.menu_close{color:#ffffff; position: absolute; right:30px; top:40px; font-size: 30px; cursor: pointer; z-index:11;}
.menu .active{color:red; font-weight: bold;}

.toggle{right:0;}